<template>
 <!-- PORTIFOLIO -->

 <h1 id="Portifolio">Portifólio</h1>

<div class="portifolio">

  <div class="btn-group" role="group" aria-label="Basic outlined example">
    <button type="button" class="btn btn-outline-warning" id="btTodos" @click="filter('todos')" :class="{active:buttons[0].status}">Todos</button>
    <button type="button" class="btn btn-outline-warning" id="btWeb" @click="filter('web')"  :class="{active:buttons[1].status}">Web</button>
    <button type="button" class="btn btn-outline-warning" id="btMobile" @click="filter('mobile')" :class="{active:buttons[2].status}">Mobile</button>
    <button type="button" class="btn btn-outline-warning" id="btDesktop" @click="filter('desktop')" :class="{active:buttons[3].status}">Desktop</button>
  </div>

  <div class="container-portifolio">

    <div class="card" style="width: 18rem;"  v-for="item in items" :key="item.message" :datafilter=item.dadosFiltro>
      <img
        :src=item.urlImg
        class="card-img-top" alt="...">
      <div class="card-body">
        <h5 class="card-title">{{ item.titulo }}</h5>
        <p class="card-text">{{ item.descricao }}</p>
      </div>
      <ul class="list-group list-group-flush">
        <li class="list-group-item">Tecnologias:</li>
        <li class="list-group-item">{{ item.tecnologias }}</li>
 
      </ul>
      <div class="card-body">
        <a :href=item.url target="_blank" class="card-link">Clique aqui para acessar o projeto</a>
      </div>
    </div>




  </div>

</div>
</template>

<script>

export default {
  name: 'PortifolioComponent',
  props: {
    msg: String
  },
  data(){
    return {
      items: [

      { 
        titulo: 'Modal Window', 
        descricao:'Janela do tipo modal',
        urlImg:"../assets/tumb-portifolio-gifs/ModalWindow.gif", 
        url:"https://claudiodenner.github.io/Projeto-Modal-Window/",
        tecnologias:"HTML, CSS, JavaScript",
        dadosFiltro:"todos/web"
        }, 

      { 
        titulo: 'Menu OffCanvas', 
        descricao:'Menu OffCanvas responsivo para mobile e desktop',
        urlImg:"../assets/tumb-portifolio-gifs/offCanvas.gif", 
        url:"https://claudiodenner.github.io/Off-Canvas-Menu/",
        tecnologias:"HTML, CSS, JavaScript",
        dadosFiltro:"todos/web"
        }, 

      { 
        titulo: 'Cronômetro JS', 
        descricao:'Cronômetro com funcionalidade de registrar paradas',
        urlImg:"../assets/tumb-portifolio-gifs/cronometroJs.gif", 
        url:"https://claudiodenner.github.io/Cronometro-com-js/",
        tecnologias:"HTML, CSS, JavaScript",
        dadosFiltro:"todos/web"
        },  

      { 
        titulo: 'PlayerMusic', 
        descricao:'Player de musica com as principais funcionalidades.',
        urlImg:"../assets/tumb-portifolio-gifs/playermusic.gif", 
        url:"https://claudiodenner.github.io/Player-Music-com-Js/",
        tecnologias:"HTML, CSS, JavaScript",
        dadosFiltro:"todos/web"
        },  

      { 
        titulo: 'Calculadora', 
        descricao:'Calculadora JS',
        urlImg:"../assets/tumb-portifolio-gifs/calculadora.gif", 
        url:"https://claudiodenner.github.io/Calculadora-com-Js/",
        tecnologias:"HTML, CSS, JavaScript",
        dadosFiltro:"todos/web"
        },  
      { 
        titulo: 'Buscador de  Coordenadas com base em Endereço', 
        descricao:'Consulta API', 
        urlImg:"../assets/tumb-portifolio-gifs/tumb-buscar-cep.gif", 
        url:"https://claudiodenner.github.io/BuscarEndereco/",
        tecnologias:"JavaScript, Bootstrap",
        dadosFiltro:"todos/web"
        },

        
      ],
      buttons:[
        {name:'todos', status:true},
        {name:'web', status:false},
        {name:'mobile', status:false},
        {name:'desktop', status:false},
      ]
    }
  },
  methods : {
      filter(param) {
          let cards = document.querySelectorAll(".card")

          cards.forEach(card => {
              if (!card.getAttribute('dataFilter').includes(param)) {
                  card.style.display = 'none'
              } else {
                  card.style.display = 'block'
              }
              this.statusButton(param)
          })
      },
      statusButton(param){
        
        
       this.buttons.forEach(bt=>{
        if(bt.name!=param){
          bt.status = false
        } else {bt.status = true}
       })

      }
  }
}
</script>

